import React from "react"

export default function Titlebar({ title, date, children, inProgress }) {
  return (
    <div>
      <Helmet>
        <title>{title}</title>
      </Helmet>

      <h2 className="center-text pt-1 blog-title">{title}</h2>
      <h4 className="center-text blog-date pb-1">{date}</h4>
      <div
        className="blog-post-title-warning"
        style={{
          visibility: inProgress ? `visible` : `hidden`,
          height: inProgress ? `10px` : `0px`,
          marginBottom: inProgress ? `40px` : `0px`,
        }}
      >
        <span className="warning-no-tooltip" />
        <p>This post is currently under construction</p>
      </div>

      <ul id="nav-container">{children}</ul>
    </div>
  )
}
